
import Vue from 'vue'
import { submitQuestionAnswer } from '@/api/detail.js'
import { uploadWechatFile } from '@/api/other.js'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'

export default {
name: 'questions',
components: {
Tab,
TabItem,
},
data () {
return {
questiontext:'',
questionid:'',
userinput:'',
picslist:[],
localIds: [], // 微信图片id
serverId: [],
picArr: [],
picArrList: [],
choose:true,
productid:'',
skuid:''
}
},
methods: {
submitanswer(){
var listpic = this.picslist.join(',')
const postData = {
questionid:this.questionid,
content:this.userinput,
pics:listpic,
}
submitQuestionAnswer(postData)
.then(res => {
if (res.data.errorcode === 0) {
this.$vux.loading.hide()
this.$vux.toast.text("提交成功", 'middle')
this.$router.push({path:"/orderDetail/questionsdetail",query:{'questionid':this.questionid,'productid':this.productid,'skuid':this.skuid}})
} else {
this.$vux.toast.text(res.data.errormsg, 'middle')
}
})
.catch(err => {
this.$vux.toast.text('请求出错！', 'middle')
})
},
chooseImg () {
const that = this
this.$wechat.chooseImage({
count: 3 - that.picArr.length, // 默认9


sizeType: ['original', 'compressed'], // 可以指定是原图还是压缩图，默认二者都有
sourceType: ['album', 'camera'], // 可以指定来源是相册还是相机，默认二者都有
success: function (res) {
that.picArr = that.picArr.concat(res.localIds)
that.localIds = that.picArr
if(that.picArr.length==3){
that.choose = false
}
}
})
},

syncUpload () {
if(this.userinput==''){
this.$vux.toast.text('请输入您的回答', 'middle')
return
}
this.$vux.loading.show()
if (!this.localIds.length) {


this.syncSubmit()
} else {
const that = this
const localId = this.localIds.pop()
that.$wechat.uploadImage({
localId: localId,
isShowProgressTips: 0,
success: function (res) {
that.serverId.push(res.serverId)
that.syncUpload()
}
})
}
},

syncSubmit () {

if (!this.serverId.length) {


this.submitanswer()
} else {
const that = this
const serverId = this.serverId.pop()
that.handleUpload(serverId)
}
},

handleUpload (val) {
const postData = {
mediaid: val
}
uploadWechatFile(postData)
.then(res => {
if (res.data.errorcode === 0) {


this.picslist.push(res.data.data.filePath)

this.syncSubmit()

} else {
this.$vux.loading.hide()

this.$vux.toast.text('图片上传失败', 'middle')
return false
}
})
.catch(err => {
console.log(err)
this.$vux.loading.hide()

this.$vux.toast.text('图片上传失败', 'middle')
return false
})
},

handleDelImg (index) {
this.picArr.splice(index, 1)
this.localIds = this.picArr
this.choose = true
console.log(this.picArr)
},
previewset (index) {
const that= this
that.$wechat.previewImage({
current: that.picArr[index], // 当前显示图片的http链接
urls: that.picArr 
});
},
},
created () {
this.questionid = this.$route.query.questionid
this.productid = this.$route.query.productid
this.skuid = this.$route.query.skuid
this.questiontext = window.localStorage.getItem("questiontext")
},
mounted(){
const that = this
Vue.wechat.ready(() => {
Vue.wechat.checkJsApi({
jsApiList: ['chooseImage', 'previewImage', 'uploadImage', 'downloadImage', 'onMenuShareTimeline', 'onMenuShareAppMessage', 'onMenuShareQQ', 'scanQRCode'], // 需要检测的JS接口列表，所有JS接口列表见附录2,
success: function (res) {
console.log(res)
}
})

const shareid = localStorage.getItem('shareid')

Vue.wechat.onMenuShareTimeline({
title: "EDIFIER问答",
link: process.env.BASE_SELF+'/weixin/index.html?#/orderDetail/questionsdetail?questionid='+that.questionid+'&productid='+that.productid+'&skuid='+that.skuid+'',
imgUrl: process.env.BASE_SELF+'/weixin/static/images/sharelogo.jpg',

success: function (res) {

}
})

Vue.wechat.onMenuShareAppMessage({
title: "EDIFIER问答",
desc: '',
link: process.env.BASE_SELF+'/weixin/index.html?#/orderDetail/questionsdetail?questionid='+that.questionid+'&productid='+that.productid+'&skuid='+that.skuid+'',
imgUrl: process.env.BASE_SELF+'/weixin/static/images/sharelogo.jpg',

success: function (res) {

}
})
})
}
}



